.wrapper{
  transition: 0.7s;
  -moz-transition: 0.7s;
  -webkit-transition: 0.7s;
  -o-transition: 0.7s;
  z-index: 2;
}
.content__wrapper{
  width: 100%;
  position: absolute;
  margin: 150px auto 0;
  text-align: center;
  font-size: 34px;
}
.logo{
  position: absolute;
  width: 55px;
  top: 10px;
  left: 5px;
}
.about__wrapper{
  width: 80%;
  position: absolute;
  margin: 150px 10%;
  text-align: center;
  font-size: 28px;
  line-height: 1.8;
}
.wrapper:after{
  content: '';
  display: block;
  clear: both;
}
.images-wrapper{  
  width: 85%;
  margin: 0 7.5%;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack:center;
      justify-content:center;
  z-index: 99;
}

.rotate-content{
  transition: 1s;
  -moz-transition: 1s;
  -webkit-transition: 1s;
  -o-transition: 1s;
  -webkit-transform-style: preserve-3d;
  -webkit-transform: translateX(-200px) translateY(0px) rotateY(50deg) scale(0.7);
  -moz-transform-style: preserve-3d;
  -moz-transform: translateX(-200px) translateY(0px) rotateY(50deg) scale(0.7);
  transform-style: preserve-3d;
  transform: translateX(-200px) translateY(0px) rotateY(50deg) scale(0.7);
}
.active{
  position: absolute;
  margin-bottom: 20px;
  -ms-transform: scale(0);
      transform: scale(0);
  -webkit-transform: scale(0);
  -moz-transform: scale(0);
  animation: showContent 0.5s ease-in 0.5s forwards;
  -webkit-animation: showContent 0.5s ease-in 0.5s forwards;

}
.active:after{
  content: '';
  display: block;
  clear: both;
}
@keyframes showContent{
  0%{  transform: scale(0); -webkit-transform: scale(0); -moz-transform: scale(0); }
  100%{transform: scale(1); -webkit-transform: scale(1); -moz-transform: scale(1); }
}
@-webkit-keyframes showContent{
  0%{  transform: scale(0); -webkit-transform: scale(0); -moz-transform: scale(0); }
  100%{transform: scale(1); -webkit-transform: scale(1); -moz-transform: scale(1); }
}
.hidden{
  position: absolute;
  animation: hiddeContent 0.5s  forwards; 
  -webkit-animation: hiddeContent 0.5s  forwards; 
}
@keyframes hiddeContent{
  0%{transform:scale(1); -webkit-transform: scale(1); -moz-transform: scale(1);}
  100%{transform:scale(0); -webkit-transform: scale(0); -moz-transform: scale(0);} 
}
@-webkit-keyframes hiddeContent{
  0%{transform:scale(1); -webkit-transform: scale(1); -moz-transform: scale(1);}
  100%{transform:scale(0); -webkit-transform: scale(0); -moz-transform: scale(0);} 
}
.sing-image_wrapper{
  width: 180px;
  margin: 20px 10px;
  position: relative;
  cursor: pointer;
}
.image{
  width: 100%;
}

.line{
  height: 3px;
  border-radius: 5px;
  background: #fff;
  margin: 10px;
  position: relative;
  transition: .5s;
  -moz-transition: .5s;
  -webkit-transition: .5s;
  -o-transition: .5s;
 
}
.line1{
  width: 38px;
}
.line2{
  width: 25px;
  margin-left: 12px;
  background: #FF0D1D;
}
.line3{
  width: 33px;
}
.move-line:nth-child(1){
  -ms-transform: rotate(90deg);
      transform: rotateZ(90deg);
  -moz-transform: rotateZ(90deg);
  -webkit-transform: rotateZ(90deg);
  position: relative;
  left: -13px;
  bottom: -11px;
}

.move-line:nth-child(3){
  margin-left: 10px;
  margin-top: -26px;
}
nav{
  width: 50%;
  right: 0;
  position: absolute;
  overflow: hidden;
  font-family: 'Arimo', sans-serif;
}
nav::after{
  content: '';
  display: block;
  clear: both;
}

.nav-links{
  text-align: center;
  padding: 0;
}
.nav-link{
  color: #fff;
  font-size: 45px;
  list-style: none;
  margin: 20px auto;
  cursor: pointer;
  position: relative;
}
.nav-link:hover{
  color: #b9b9b9;
}
.active__link{
  color:#FF0D1D;
}
.active__link:after{
  content: '';
  display: block;
  clear: both;
  margin: 0 auto;
  background: #FF0D1D;
  position: absolute;
  bottom: 25px;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  animation: activeLink 0.7s forwards; 
  -webkit-animation: activeLink 0.7s forwards; 
}
@keyframes activeLink{
  from{
    height: 4px;width: 0px;
  }
  to{
    height: 4px;width: 240px;
  }
}
@-webkit-keyframes activeLink{
  from{
    height: 4px;width: 0px;
  }
  to{
    height: 4px;width: 240px;
  }
}
.menu{
  position: absolute;
  right: 0px;
  top: 0px;
  margin: 10px;
  height: 50px;
  cursor: pointer;
  z-index: 999;
}
.menu-wrapper{
  margin-top: 35px;
  text-align: center;
  padding-bottom: 50px;
  overflow: hidden;
}
.open{
  animation: openMenu 1s forwards;
  -webkit-animation: openMenu 1s forwards;
}
.close{
  transition:1s;
  -moz-transition:1s;
  -webkit-transition:1s;
  -o-transition:1s;
  animation: closeMenu 1s forwards;
  -webkit-animation: closeMenu 1s forwards;
}
@keyframes openMenu {
  from{display: none; margin-left: 100%}
    to{display: block;margin-left: 0%}
}
@-webkit-keyframes openMenu {
  from{display: none; margin-left: 100%}
    to{display: block;margin-left: 0%}
}
@keyframes closeMenu {
  from{display: block; margin-left: 0%}
    to{display: none;margin-left: 100%}
}
@-webkit-keyframes closeMenu {
  from{display: block; margin-left: 0%}
    to{display: none;margin-left: 100%}
}
.full-image_wrapper{
  max-width: 65%;
  position: fixed;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%,-50%);
      transform: translate(-50%,-50%);
  -moz-transform: translate(-50%,-50%);
  -webkit-transform: translate(-50%,-50%);
  animation: viewImage 1s forwards;
  -webkit-animation: viewImage 1s forwards;
  z-index: 1000;
}

@keyframes viewImage {
  from{width: 10%}
  to{width:50%}
}
@-webkit-keyframes viewImage {
  from{width: 10%}
  to{width:50%}
}

.full-image_bc-filter{
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 999;
  -webkit-animation:filter 1s ease-out forwards;
          animation:filter 1s ease-out forwards;
}

@keyframes filter {
  from{background: rgba(0,0,0,0)}
  to{background: rgba(0,0,0,0.9)}
}


@-webkit-keyframes filter {
  from{background: rgba(0,0,0,0)}
  to{background: rgba(0,0,0,0.9)}
}

.close-full_image{
  color: #fff;
  cursor: pointer;
  position: absolute;
  top: 0px;
  right: 0px;
  font-size: 25px;
  padding: 10px 15px;
  background: rgba(0,0,0,0.3);
}
.close-full_image:hover{
  background: rgba(0,0,0,0.8);
}
.full-image{
  width: 100%;
}
.next, .prev{
  cursor: pointer;
  color:#fff;
  background:rgba(0,0,0,0.5);
  width: 20px;
  padding: 10px 20px;
  position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
      transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
}
.next{
  right: 0;

}
.prev{
  left: 0;
}
.next:hover, .prev:hover{
  cursor: pointer;
  background:rgba(0,0,0,0.8);
}
.container{
  transition: 1s;
  -moz-transition: 1s;
  -webkit-transition: 1s;
  -o-transition: 1s;
  -webkit-perspective: 1000px;
  perspective: 1000px;
}
/* .filter{
  transition: 1s;
  -moz-transition: 1s;
  -webkit-transition: 1s;
  -o-transition: 1s;
  filter: brightness(20%);
  -webkit-filter: brightness(20%);
} */

.soc__media--wrapper{
  display: inline-block;
  width: 100px;
}
.soc__media--icon{
  width:100%;
}
.soc__media--icon:hover{
  filter: brightness(0.5);
  -webkit-filter: brightness(0.5);
}
@media screen and (max-width:934px) {
  .full-image_wrapper{
    max-width: 90%;
  }
  @keyframes viewImage {
    from{width: 10%}
    to{width:100%}
  }
  @-webkit-keyframes viewImage {
    from{width: 10%}
    to{width:100%}
  }
  .rotate-content {
    -webkit-transform: translateX(-190px) translateY(0px) rotateY(45deg) scale(0.6);
    transform:  translateX(-190px) translateY(0px) rotateY(45deg) scale(0.6);
  }
}

@media screen and (max-width:800px) {
  .rotate-content {
    -webkit-transform: translateX(-135px) translateY(0pc) rotateY(45deg) scale(0.6);
    transform:  translateX(-135px) translateY(0px) rotateY(45deg) scale(0.6);
  }
}

@media screen and (max-width:722px) {
  .rotate-content {
    -webkit-transform:  translateX(-110px) translateY(0px) rotateY(45deg) scale(0.7);
    transform: translateX(-110px) translateY(0px) rotateY(45deg) scale(0.7);
  }
  .nav-link {
    font-size: 36px;
  }
  @keyframes activeLink{
    from{bottom: 23px; height: 4px;width: 0px;}
    to{height: 4px;width: 200px;bottom: 23px;}
  }
  @-webkit-keyframes activeLink{
    from{bottom: 23px; height: 4px;width: 0px;}
    to{height: 4px;width: 200px;bottom: 23px;}
  }

}
@media screen and (max-width:520px) {
  @media screen and (max-width:722px) {
    .active__link:after {
      top: 18px;
    }
  }
  .sing-image_wrapper {
    width: 160px;
  }
  .content__wrapper{
    margin: 50px auto 0;
  }
  .about__wrapper{
    margin: 0px 10%;
  }
  .next, .prev {
    padding: 5px 10px;
  }
  .soc__media--wrapper {
    width: 115px;
  }
}
@media screen and (max-width:490px) {
  nav{
    width: 100%;
    overflow: unset;
  }
  .full-image_wrapper{
    max-width: 98%;
  }
  .sing-image_wrapper {
    width: 180px;
  }
  .images-wrapper {
    width: 85%;
  }
  .close-full_image{
    padding: 5px 10px;
  }
  .rotate-content {
    -webkit-transform: translateY(300px) translateZ(0px) rotateX(-45deg);
    transform: translateY(300px) translateZ(0px) rotateX(-45deg);
  }
  .content__wrapper {
    font-size: 22px;
  }
  @keyframes openMenu {
    from{display: none; margin-top: -100%}
      to{display: block;margin-top: 0%}
  }
  @-webkit-keyframes openMenu {
    from{display: none; margin-top: -100%}
      to{display: block;margin-top: 0%}
  }
  @keyframes closeMenu {
    from{display: block; margin-top: 0%}
      to{display: none;margin-top: -100%}
  }
  @-webkit-keyframes closeMenu {
    from{display: block; margin-top: 0%}
      to{display: none;margin-top: -100%}
  }
}



